.footer{
    width: 100%;
    padding-bottom: 60px;
    &__wrapp{
        display: flex;
        justify-content: space-between;
        gap: 16px;

        img{
            display: block;
            width: 59px;
            height: 34px;
            object-fit: cover;
        }

        a{
            display: block;
            max-width: 305px;
            font-family: "Inter", sans-serif;
            font-size: 12px;
            line-height: 130%;
            letter-spacing: 1.2px;
            font-weight: 400;
            color: #FFFFFF;
        }
    }
}